
@import 'src/assets/styles/_variables';


.menu{
    display: flex;
    padding-right: 15px;
    gap: 45px;
}

.list{
    display: flex;
    justify-content: space-between;
    align-items:center; 
}
    
.showLogo{
    display: block;
    width: 50px;
    height: 50px;
    cursor: pointer;
}

.hideLogo{
    display: none;
    width: 50px;
    height: 50px;
}
